@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap");
body {
  font-size: 14px;
  font-family: Lora, serif;
}

* {
  box-sizing: border-box;
}

.root {
  padding: 5px 10px;
  font-size: 8px;
  border-radius: 4px;
  color: white;
}
.root_primary {
  background: #B09A81;
}
.root_secondary {
  background: #764051;
}
.root_success {
  background: #1F4F3B;
}
.root_info {
  background: #123856;
}
.root_warning {
  background: #cba34a;
}
.root_danger {
  background: #512C38;
}