@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap");
body {
  font-size: 14px;
  font-family: Lora, serif;
}

* {
  box-sizing: border-box;
}

.root {
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #462929;
}
.root .input {
  display: none;
}
.root .checkbox {
  display: block;
  height: 15px;
  width: 15px;
  border-radius: 2px;
  border: 2px solid #462929;
  box-sizing: border-box;
  margin-right: 5px;
  transition: border-color 0.1s ease-in-out, background-color 0.1s ease-in-out;
}
.root .checkbox_checked {
  border-color: #1F4F3B;
  background-color: #1F4F3B;
  color: #B09A81;
  font-size: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.root .checkbox_checked::after {
  content: "âœ“";
}
.root .checkbox_disabled {
  border-color: #796A58 !important;
  background-color: #796A58;
}
.root:hover .checkbox {
  border-color: #1F4F3B;
}